import React from 'react';
import { Link } from "react-router-dom";
import styled from 'styled-components';
import { useDispatch } from 'react-redux';
import { toggleCart, openHeaderModal } from '../slices/shopSlice';
import { FaShoppingCart } from 'react-icons/fa';
import { IconContext } from "react-icons";
import Logo from './Logo';



export default function () {
    const dispatch = useDispatch();
    return (
        <HeaderWrapper>
            <LeftHeaderContainer><Logo /></LeftHeaderContainer>
            <MidHeaderContainer>
                <HeaderLink><Link style={{ 'text-decoration': 'none' }} to="/">HOME</Link></HeaderLink>
                <HeaderLink><Link style={{ 'text-decoration': 'none' }} to="/full-shop">FULL SHOP</Link></HeaderLink>
                <HeaderLink onMouseOver={() => { dispatch(openHeaderModal()) }} ><Link style={{ 'text-decoration': 'none' }} to="/">WHAT'S eHARVEST</Link></HeaderLink> </MidHeaderContainer>
            <RightHeaderContainer>
                <IconContext.Provider value={{ style:{ 'margin-right':'10px', color:'blue', cursor:'pointer'} }}  >
                
                    <FaShoppingCart size={30} onClick={() => { dispatch(toggleCart()) }} />
                
                </IconContext.Provider>
            </RightHeaderContainer>
        </HeaderWrapper>

    )
}

const HeaderWrapper = styled.div`
display:flex;
justify-content: center;
align-items: center;
width:100%;
height:200px;
position: fixed;
top:0;
left:0;
background:yellow;
text-align:center;
z-index:2;


`
const LeftHeaderContainer = styled.div`

display:flex;
justify-content: flex-start;
align-items: center;
width:30%;
background:violet;

`
const RightHeaderContainer = styled.div`

display:flex;
justify-content: flex-end;
align-items: center;
width:30%;

`
const MidHeaderContainer = styled.div`

display:flex;
justify-content: center;
align-items: center;
width:40%;

`



const HeaderLink = styled.div`
margin:10px;
`
